import React from 'react';
import { Link } from 'react-router-dom';

import Main from '../layouts/Main';
import Courses from '../components/Resume/Courses';
import EductionComponent from '../components/Resume/Education';

import courses from '../data/education/courses';
import degrees from '../data/education/degrees';

const sections = {
  Education: () => <EductionComponent data={degrees} />,
  Courses: () => <Courses data={courses} />,
};

const Education = () => {
  const gobearsUrl = `${process.env.PUBLIC_URL}images/gobears.jpg`;
  return (
    <Main
      title="Eduction"
      description=""
      fullPage="true"
    >
      <article className="post markdown" id="education">
        <header>
          <div className="title">
            <h2><Link to="/education">Education</Link></h2>
          </div>
        </header>
        <div className="education-container">
          <div className="left-hand-side">
            {Object.entries(sections).map(([name, Section]) => (
              <Section key={name} />
            ))}
          </div>
          <img className="go-bears" alt="gobears" src={gobearsUrl} />
        </div>
      </article>
    </Main>
  );
};

export default Education;
